.mapScreen {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
  position: relative; }
  .mapScreen header {
    height: 50px;
    display: flex;
    justify-content: center; }
    .mapScreen header button {
      border: 1px solid black;
      margin: 10px 0;
      padding: 0 50px;
      background-color: transparent;
      line-height: 1;
      font-size: 14px; }
      .mapScreen header button:first-child {
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px; }
      .mapScreen header button:last-child {
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px; }
    .mapScreen header .buttonActive {
      background-color: #000000;
      color: #ffffff; }
    .mapScreen header .buttonInactive {
      background-color: #ffffff;
      color: #000000; }
  .mapScreen .mapComponent {
    height: calc(100vh - (70px + 50px));
    box-sizing: border-box; }
  .mapScreen .gmnoprint, .mapScreen .gm-control-active {
    display: none; }
  .mapScreen .markerInfos {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
    background-color: #ffffff;
    padding: 15px 20px;
    transition: .3s; }
    .mapScreen .markerInfos .title {
      text-align: center;
      font-size: 12px;
      font-weight: bold; }
    .mapScreen .markerInfos .infos {
      margin: 20px 0;
      font-size: 12px;
      display: grid;
      grid-template-areas: "a b" "c c"; }
      .mapScreen .markerInfos .infos ul li {
        list-style-type: none; }
      .mapScreen .markerInfos .infos div:last-child {
        margin-top: 30px; }
    .mapScreen .markerInfos button:last-child {
      background-color: #000000;
      color: #ffffff;
      width: 100%;
      padding: 15px 30px; }
  .mapScreen .exitButton {
    width: 26px;
    height: 26px;
    position: absolute;
    top: 10px;
    right: 10px;
    border: 0;
    background-color: transparent; }
    .mapScreen .exitButton::before, .mapScreen .exitButton::after {
      content: "";
      width: 32px;
      height: 2px;
      background-color: #000000;
      display: block;
      position: absolute;
      top: 11px;
      left: -4px; }
    .mapScreen .exitButton::before {
      transform: rotate(45deg); }
    .mapScreen .exitButton::after {
      transform: rotate(-45deg); }

.new header {
  margin-top: 48px;
  padding: 20px 0;
  background-color: #F1F1F1; }
  .new header ul li {
    display: inline-block;
    position: relative;
    width: calc(100% / 3); }
    .new header ul li p:first-child {
      font-size: 11px;
      font-weight: bold; }
    .new header ul li p:last-child {
      margin: 5px auto 0 auto;
      background-color: #000000;
      color: #ffffff;
      width: 25px;
      height: 25px;
      line-height: 23px;
      font-size: 14px;
      z-index: 1;
      position: relative;
      font-weight: bold; }
    .new header ul li:not(:first-child)::after {
      content: '';
      width: 35%;
      height: 1px;
      background-color: #dbdbdb;
      display: block;
      position: absolute;
      bottom: 12px;
      left: 0; }
    .new header ul li:not(:last-child)::before {
      content: '';
      width: 35%;
      height: 1px;
      background-color: #dbdbdb;
      display: block;
      position: absolute;
      bottom: 12px;
      right: 0; }
  .new header .activeStep p:last-child {
    border: 2px solid #FFCD2A; }
  .new header .inactiveStep p:first-child {
    opacity: .3; }
  .new header .inactiveStep p:last-child {
    border: 2px solid #f1f1f1;
    opacity: .3; }

.new main {
